.burger {
  @include burger;

  z-index: 20;
  display: none;

  @include tablet {
    display: block;
  }
}

.header__inner {
  &.menu--active {
    position: absolute;
    z-index: 10;
    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-end;
    padding: 100px 20px 30px;
    background-color: var(--light-color);
    inset: 0;

    .header__top {
      flex-direction: column;
      align-items: center;

      &::before {
        display: none;
      }
    }

    .header__nav {
      margin-bottom: 60px;
    }

    .header__nav-list {
      flex-direction: column;
      align-items: center;
    }

    .header__nav-item {
      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 30px;
      }
    }

    .header__tel {
      margin-bottom: 30px;
    }

    .header__btn {
      display: none;
    }
  }

  @include tablet {
    display: none;
  }
}

.burger-wrap {
  position: relative;
  display: none;
  margin-top: 20px;
  width: 48px;
  height: 48px;

  &::before {
    content: "";
    position: absolute;
    left: -6px;
    top: -12px;
    z-index: 10;
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgb(0 177 201 / 8%);
  }

  @include tablet {
    display: block;
  }
}
