.resident {
  padding-bottom: 160px;

  &__container {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      bottom: -45px;
      width: 96.5%;
      height: 1px;
      background-color: var(--lightdark-color);
      transform: translateX(-50%);

      @include tablet {
        display: none;
      }
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 36px;
    line-height: 150%;
    color: var(--dark-color);

    @include tablet {
      margin-bottom: 30px;
      font-size: 32px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 24px;
      line-height: 150%;
    }
  }
}

.resident-slider {
  position: relative;
  margin-top: -60px;
  padding-top: 60px;

  .swiper-slide {
    overflow: hidden;
    height: calc((100% - 30px) / 2);
  }

  &__img {
    margin-bottom: 35px;
  }

  &__btns {
    position: absolute;
    right: 0;
    top: 5px;

    .swiper-button-disabled {
      fill: var(--lightdark-color);
    }

    @include tablet {
      left: 0;
      right: inherit;
      top: inherit;
      bottom: 3px;
    }
  }

  &__btn {
    width: 75px;
    height: 15px;
    fill: var(--blue-color);

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__prev {
    margin-right: 30px;
  }

  &__slide {
    position: relative;
    display: block;
    margin: 4px;
    outline: solid 1px var(--lightdark-color);
    padding: 30px;
    transition: outline 0.2s ease;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 150px;
      width: 90%;
      height: 1px;
      background-color: var(--lightdark-color);
      transform: translateX(-50%);
    }

    &:hover {
      outline: 2px solid var(--blue-color);
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
    text-transform: uppercase;
    color: var(--dark-color);
  }

  &__date {
    display: inline-block;
    margin-bottom: 16px;
    font-size: 15px;
    line-height: 130%;
    color: var(--grey-color);
  }

  &__text {
    margin: 0;
    margin-bottom: 22px;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark-color);
  }

  &__link {
    font-size: 17px;
    line-height: 150%;
    color: var(--blue-color);
  }

  @include tablet {
    margin-top: 0;
    margin-bottom: -60px;
    padding-top: 0;
    padding-bottom: 60px;
    font-size: 32px;
    line-height: 150%;
  }
}
