.new {
  &__title {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 36px;
    line-height: 150%;
    color: var(--dark-color);

    @include tablet {
      font-size: 32px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 24px;
      line-height: 150%;
    }
  }
}

.new-slider {
  position: relative;
  margin-top: -60px;
  padding-top: 75px;

  &__img {
    margin-bottom: 16px;
    height: 300px;
  }

  &__btns {
    position: absolute;
    right: 0;
    top: 5px;

    .swiper-button-disabled {
      fill: var(--lightdark-color);
    }

    @include mobile {
      left: 0;
      top: inherit;
      bottom: 32px;
    }
  }

  &__btn {
    width: 75px;
    height: 15px;
    fill: var(--blue-color);

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__prev {
    margin-right: 30px;
  }

  &__slide {
    position: relative;
    display: block;
    margin-bottom: 110px;
    transition: outline 0.2s ease;

    @include tablet {
      margin-bottom: 75px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    color: var(--dark-color);

    @include tablet {
      font-size: 20px;
      line-height: 150%;
    }
  }

  &__date {
    display: inline-block;
    margin-bottom: 16px;
    font-size: 20px;
    line-height: 150%;
    color: rgb(49 49 49 / 40%);

    @include mobile {
      font-size: 15px;
      line-height: 150%;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 22px;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark-color);

    @include tablet {
      font-size: 17px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 15px;
      line-height: 150%;
    }
  }

  &__link {
    font-size: 20px;
    line-height: 150%;
    color: var(--blue-color);

    @include mobile {
      font-size: 15px;
      line-height: 150%;
    }
  }
}
