.footer {
  margin-bottom: 30px;
  padding-top: 50px;

  &__top {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 35px;

    @include mobile {
      flex-direction: column;
      align-items: center;
    }
  }

  &__logo {
    margin-bottom: 20px;
    width: 200px;
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-right: 100px;

    @include tablet {
      margin-right: 0;
    }

    @include mobile {
      margin-bottom: 30px;
    }
  }

  &__nav-list {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include desktop {
      flex-wrap: wrap;
      align-items: center;
      justify-content: flex-end;
      margin-right: 21px;
      gap: 10px;
    }

    @include mobile {
      justify-content: space-between;
      margin-right: 0;
    }
  }

  &__nav-item {
    &:not(:last-child) {
      margin-right: 50px;

      @include desktop {
        margin-right: 0;
      }

      @include mobile {
        flex-grow: 1;
      }
    }

    @include desktop {
      width: 40%;
    }
  }

  &__nav-link {
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
    color: var(--dark-color);
  }

  &__social {
    display: flex;
    justify-content: space-between;

    @include tablet {
      flex-direction: column;
    }

    @include mobile {
      flex-direction: row;
    }
  }

  &__social-item {
    &:not(:last-child) {
      margin-right: 30px;

      @include tablet {
        margin-right: 0;
        margin-bottom: 20px;
      }

      @include mobile {
        margin-right: 20px;
        margin-bottom: 0;
      }
    }
  }

  &__social-image {
    width: 40px;
    height: 40px;
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include tablet {
      flex-direction: column;
      align-items: flex-start;
    }

    @include mobile {
      align-items: center;
    }
  }

  &__bottom-text {
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
    color: var(--dark-color);

    @include tablet {
      margin-bottom: 10px;
    }

    @include mobile {
      margin-bottom: 15px;
    }
  }

  &__bottom-descr {
    display: flex;
    align-items: center;

    @include tablet {
      justify-content: space-between;
      width: 100%;
    }

    @include mobile {
      flex-direction: column;
      align-items: center;
    }
  }

  &__bottom-link {
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #393939;

    &:not(:last-child) {
      margin-right: 90px;

      @include mobile {
        margin-right: 0;
        margin-bottom: 10px;
      }
    }
  }

  &__bottom-link--flex {
    display: flex;
    align-items: center;
  }

  &__bottom-img {
    margin-left: 10px;
    width: 20px;
    height: 20px;
    fill: var(--dark-color);
  }

  @include tablet {
    margin-bottom: 20px;
    padding-top: 100px;
  }
}
