.questions {
  position: relative;
  left: 50%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 50px;
  width: 108%;
  background-color: var(--lightblue-color);
  transform: translateX(-50%);

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    @include tablet {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 12px;
    font-weight: 700;
    font-size: 30px;
    line-height: 130%;
    color: var(--dark-color);

    @include tablet {
      font-size: 24px;
      line-height: 130%;
    }
  }

  &__text {
    margin: 0;
    font-size: 20px;
    line-height: 130%;
    color: var(--dark-color);

    @include tablet {
      margin-bottom: 30px;
      font-size: 17px;
      line-height: 130%;
    }
  }

  &__text--width {
    max-width: 665px;
  }

  &__btn {
    display: inline-block;
    padding: 20px 36px;
    font-family: "Inter", sans-serif;
    font-weight: 700;
    font-size: 17px;
    line-height: 130%;
    text-transform: uppercase;
    color: var(--light-color);
    background-color: var(--blue-color);

    @include tablet {
      font-size: 15px;
      line-height: 130%;
    }

    @include mobile {
      padding: 20px;
      width: 100%;
    }
  }

  @include tablet {
    flex-direction: column;
    align-items: flex-start;
    padding: 30px;
  }

  @include mobile {
    padding: 30px 25px;
  }
}
