.map {
  position: relative;

  #map {
    height: 710px;

    @include mobile {
      height: 480px;

      .ymaps-2-1-79-map {
        height: 480px;
      }
    }
  }

  &__wrapper {
    position: absolute;
    left: 50%;
    top: 90px;
    padding: 44px;
    width: 100%;
    max-width: 595px;
    background-color: var(--light-color);
    transform: translateX(-100%);

    @include desktop {
      left: 20px;
      transform: translateX(0%);
    }

    @include tablet {
      left: 50%;
      top: inherit;
      bottom: 0;
      transform: translateX(-50%);
    }

    @include mobile {
      position: relative;
      left: 0;
      padding: 20px 0 0;
      transform: translateX(0);
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 30px;
    line-height: 130%;
    color: var(--dark-color);

    @include mobile {
      font-size: 24px;
      line-height: 130%;
    }
  }

  &__list {
    margin-bottom: 45px;
  }

  &__tel {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 35px;
    padding-left: 60px;
    font-weight: 400;
    font-size: 24px;
    line-height: 150%;
    color: var(--dark-color);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 40px;
      height: 40px;
      background-image: url("../img/tel.svg");
      background-size: 40px 40px;
      background-repeat: no-repeat;

      @include mobile {
        top: -6px;
      }
    }

    @include mobile {
      font-size: 17px;
      line-height: 150%;
    }
  }

  &__mail {
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 60px;
    font-weight: 400;
    font-size: 24px;
    line-height: 150%;
    color: var(--dark-color);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 40px;
      height: 40px;
      background-image: url("../img/mail.svg");
      background-size: 40px 40px;
      background-repeat: no-repeat;

      @include mobile {
        top: -6px;
      }
    }

    @include mobile {
      font-size: 17px;
      line-height: 150%;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 25px;
    font-weight: 700;
    font-size: 24px;
    line-height: 150%;
    color: var(--dark-color);
  }

  &__address {
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 60px;
    font-weight: 400;
    font-style: normal;
    font-size: 24px;
    line-height: 150%;
    color: var(--dark-color);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 40px;
      height: 40px;
      background-image: url("../img/address.svg");
      background-size: 40px 40px;
      background-repeat: no-repeat;
    }

    @include mobile {
      font-size: 17px;
      line-height: 150%;
    }
  }

  @include tablet {
    padding-bottom: 315px;
  }

  @include mobile {
    padding-bottom: 0;
  }
}
