.transport {
  padding-top: 50px;
  padding-bottom: 100px;

  &__container {
    display: flex;

    @include tablet {
      flex-direction: column;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-size: 36px;
    line-height: 150%;
    color: var(--dark-color);

    @include tablet {
      width: 51%;
      font-size: 32px;
      line-height: 150%;
    }

    @include mobile {
      margin-bottom: 20px;
      width: 100%;
      font-size: 24px;
      line-height: 150%;
    }
  }

  &__text {
    margin: 0;
    font-size: 17px;
    line-height: 150%;
    color: var(--dark-color);

    @include tablet {
      margin-bottom: 35px;
      font-size: 17px;
      line-height: 150%;
    }

    @include mobile {
      margin-bottom: 20px;
      font-size: 15px;
      line-height: 150%;
    }
  }

  &__inner {
    display: grid;
    flex: 1 0 auto;
    grid-template-columns: repeat(4, 1fr);
    border: 1px solid var(--lightdark-color);
    width: calc(100% - 520px);

    @include tablet {
      width: 100%;
    }
  }

  &__item {
    grid-column: 2 span;
    padding-right: 24px;
    padding-bottom: 24px;
    padding-left: 24px;

    &:first-child {
      border-right: 1px solid var(--lightdark-color);
    }

    &:last-child {
      grid-column: 4 span;
      border-top: 1px solid var(--lightdark-color);
      padding-top: 24px;

      @include mobile {
        grid-column: 4 span;
      }
    }

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__item--pic1 {
    padding-top: 90px;
    background-image: url("../img/icon-15.svg");
    background-position: 20px 25px;
    background-size: 52px 52px;
    background-repeat: no-repeat;
  }

  &__item-text {
    margin: 0;
    margin-bottom: 10px;
    font-size: 15px;
    line-height: 130%;
    color: var(--grey-color);

    br {
      @include mobile {
        display: none;
      }
    }
  }

  &__item-num {
    margin: 0;
    font-weight: 700;
    font-size: 28px;
    line-height: 130%;
    color: var(--dark-color);
  }

  &__inner-text {
    margin-left: 24px;
    font-weight: 400;
    font-size: 17px;
    line-height: 150%;
    color: var(--dark-color);

    @include mobile {
      font-size: 15px;
      line-height: 150%;
    }
  }

  &__inner-words {
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 17px;
    line-height: 150%;
    color: var(--dark-color);
  }

  &__inner-list {
    list-style-type: disc;
  }

  @include tablet {
    padding-top: 60px;
    padding-bottom: 50px;
  }

  @include mobile {
    padding-top: 70px;
    padding-bottom: 60px;
  }
}
