.rule {
  padding-bottom: 100px;

  &__title {
    margin: 0;
    margin-bottom: 30px;
    max-width: 590px;
    font-weight: 700;
    font-size: 36px;
    line-height: 150%;
    color: var(--dark-color);

    @include tablet {
      font-size: 32px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 24px;
      line-height: 150%;
    }
  }

  &__list {
    counter-reset: counter-rule;
    display: flex;
    gap: 30px;

    @include tablet {
      flex-direction: column;
    }
  }

  &__item {
    width: 33.3%;

    &::before {
      counter-increment: counter-rule;
      content: counter(counter-rule);
      display: inline-flex;
      flex: 1 0 auto;
      align-items: center;
      justify-content: center;
      margin-bottom: 25px;
      width: 57px;
      height: 54px;
      font-weight: 700;
      font-size: 28px;
      line-height: 130%;
      color: var(--blue-color);
      background-color: rgb(0 177 201 / 8%);
    }

    @include tablet {
      width: 100%;
    }
  }

  &__text {
    margin: 0;
    font-size: 20px;
    line-height: 150%;

    @include tablet {
      font-size: 17px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 15px;
      line-height: 150%;
    }
  }

  @include tablet {
    padding-bottom: 80px;
  }
}
