.header {
  padding-top: 10px;
  padding-bottom: 22px;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    flex: 1 0 auto;

    @include tablet {
      position: relative;
      z-index: 11;
      flex: inherit;
    }

    @include mobile {
      width: 100px;
    }
  }

  &__top {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-bottom: 22px;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      bottom: -10px;
      width: 100%;
      height: 1px;
      background-color: var(--lightdark-color);
      transform: translateX(-50%);
    }
  }

  &__tel {
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 60px;
    font-weight: 700;
    font-size: 17px;
    line-height: 23px;
    color: var(--dark-color);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 40px;
      height: 40px;
      background-image: url("../img/tel.svg");
      background-size: 40px 40px;
      background-repeat: no-repeat;
    }
  }

  &__tel--mob {
    display: none;

    &::before {
      content: "";
      top: -7px;
    }

    @include tablet {
      display: block;
    }

    @include mobile {
      font-size: 0;
    }
  }

  &__mail {
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 60px;
    font-weight: 500;
    font-size: 17px;
    line-height: 23px;
    color: var(--dark-color);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 40px;
      height: 40px;
      background-image: url("../img/mail.svg");
      background-size: 40px 40px;
      background-repeat: no-repeat;
    }
  }

  &__btn {
    display: inline-block;
    padding: 12px 32px;
    font-family: "Inter", sans-serif;
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;
    text-transform: uppercase;
    color: var(--light-color);
    background-color: var(--blue-color);
  }

  &__nav-list {
    display: flex;
    justify-content: space-between;
  }

  &__nav-item {
    &:not(:last-child) {
      margin-right: 125px;

      @include desktop {
        margin-right: 80px;
      }
    }
  }

  &__nav-link {
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
    color: var(--dark-color);
  }

  @include tablet {
    padding-top: 15px;
  }

  @include mobile {
    padding-top: 3px;
    padding-bottom: 0;
  }
}
