.technological {
  padding-bottom: 100px;

  &__title {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 36px;
    line-height: 150%;
    color: var(--blue-color);

    @include tablet {
      margin-bottom: 20px;
      font-size: 32px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 24px;
      line-height: 150%;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 30px;
    max-width: 900px;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark-color);

    @include tablet {
      font-size: 17px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 15px;
      line-height: 150%;
    }
  }

  &__block {
    &:not(:last-child) {
      margin-bottom: 105px;

      @include tablet {
        margin-bottom: 45px;
      }
    }
  }

  &__block-wrap {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 30px;

    picture {
      @include tablet {
        width: 100%;
      }
    }

    @include tablet {
      flex-direction: column;
    }
  }

  &__block-wrap--reverse {
    flex-direction: row-reverse;

    @include tablet {
      flex-direction: column;
    }
  }

  &__block-image {
    @include tablet {
      width: 100%;
      height: 350px;
    }
  }

  &__block-image--width {
    height: 640px;

    @include tablet {
      width: 100%;
      height: 350px;
    }
  }

  &__block-descr {
    width: 50%;

    @include tablet {
      width: 100%;
    }
  }

  &__block-title {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 600;
    font-size: 28px;
    line-height: 150%;
    color: var(--dark-color);

    @include tablet {
      font-size: 24px;
      line-height: 150%;
    }

    @include mobile {
      margin-bottom: 10px;
      font-size: 20px;
      line-height: 150%;
    }
  }

  &__block-text {
    margin: 0;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark-color);

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    &:last-child {
      margin-bottom: 30px;
    }

    @include tablet {
      font-size: 17px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 15px;
      line-height: 150%;
    }
  }

  &__block-hidden {
    margin-top: -25px;

    @include tablet {
      margin-top: 0;
    }
  }

  &__block-btn {
    font-size: 20px;
    line-height: 150%;
    color: var(--blue-color);

    @include tablet {
      font-size: 17px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 15px;
      line-height: 150%;
    }
  }

  &__block-btn--end {
    width: 100%;
    text-align: end;

    @include tablet {
      text-align: left;
    }
  }

  @include mobile {
    padding-bottom: 65px;
  }
}

.left {
  text-align: left;
}

.technological-hidden {
  &__content {
    position: relative;
    margin-top: 80px;
    margin-bottom: 30px;

    &::after {
      content: "";
      position: absolute;
      right: 0;
      bottom: -45px;
      display: block;
      width: 90%;
      height: 1px;
      background-color: var(--lightdark-color);
    }

    @include tablet {
      margin-top: 45px;
    }
  }

  &__list {
    counter-reset: counter-tech;
    list-style-type: none;
    margin-bottom: 30px;
  }

  &__item {
    position: relative;
    padding-left: 80px;

    &::before {
      counter-increment: counter-tech;
      content: counter(counter-tech);
      position: absolute;
      left: 0;
      top: 0;
      display: inline-flex;
      flex: 1 0 auto;
      align-items: center;
      justify-content: center;
      margin-right: 30px;
      width: 57px;
      height: 54px;
      font-weight: 700;
      font-size: 28px;
      line-height: 130%;
      color: var(--blue-color);
      background-color: rgb(0 177 201 / 8%);
    }

    &:not(:last-child) {
      margin-bottom: 40px;

      @include tablet {
        margin-bottom: 30px;
      }
    }

    @include mobile {
      padding-top: 65px;
      padding-left: 0;
    }
  }

  &__item-text {
    margin: 0;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark-color);

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    @include tablet {
      font-size: 17px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 15px;
      line-height: 150%;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 30px;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark-color);

    @include tablet {
      font-size: 17px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 15px;
      line-height: 150%;
    }
  }

  &__more {
    position: relative;
    left: 50%;
    padding: 30px 50px;
    width: 108%;
    background-color: var(--lightblue-color);
    transform: translateX(-50%);

    @include tablet {
      padding: 30px;
    }

    @include mobile {
      padding: 30px 20px;
      width: 112%;
    }
  }

  &__more-title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;

    @include tablet {
      font-size: 20px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 17px;
      line-height: 150%;
    }
  }

  &__more-text {
    margin: 0;
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;

    &:not(:last-child) {
      margin-bottom: 30px;
    }

    @include tablet {
      font-size: 17px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 15px;
      line-height: 150%;
    }
  }

  &__more-list {
    list-style-type: disc;
    margin-bottom: 20px;

    @include tablet {
      flex-wrap: wrap;
    }
  }

  &__more-item {
    flex: 1 0 0;
    margin-left: 24px;
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark-color);

    @include tablet {
      font-size: 17px;
      line-height: 150%;
    }

    @include mobile {
      flex: inherit;
      font-size: 15px;
      line-height: 150%;
    }
  }

  &__more-item--width {
    @include tablet {
      flex: inherit;
      width: 41%;
    }

    @include mobile {
      width: 100%;
    }
  }
}
