.communal {
  padding-top: 50px;
  padding-bottom: 25px;

  &__wrapper {
    display: flex;

    @include tablet {
      flex-direction: column;
    }
  }

  &__title {
    margin: 0;
    margin-right: 75px;
    width: calc(100% - 40px);
    font-weight: 700;
    font-size: 36px;
    line-height: 150%;
    color: var(--dark-color);

    @include tablet {
      margin-right: 0;
      margin-bottom: 30px;
      width: 50%;
      font-size: 32px;
      line-height: 150%;
    }

    @include mobile {
      margin-bottom: 20px;
      width: 100%;
      font-size: 24px;
      line-height: 150%;
    }
  }

  &__text {
    margin: 0;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark-color);

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    @include tablet {
      font-size: 17px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 15px;
      line-height: 150%;
    }
  }

  @include tablet {
    padding-top: 25px;
    padding-bottom: 60px;
  }
}
