.review {
  padding: 65px 0 95px;
  background-color: var(--blue-color);

  &__list {
    position: relative;
    left: 53%;
    display: grid;
    grid-template-columns: repeat(11, 1fr);
    width: 110%;
    transform: translateX(-50%);
    gap: 50px 20px;

    @include desktop {
      left: 0;
      grid-template-columns: repeat(12, 1fr);
      width: 100%;
      transform: translateX(0%);
    }

    @include tablet {
      margin-left: 25px;
    }

    @include mobile {
      margin-left: 0;
    }
  }

  &__item {
    grid-column: span 2;
    padding-top: 60px;
    background-size: 52px 52px;
    background-repeat: no-repeat;

    &:nth-child(1) {
      grid-column: span 3;

      @include desktop {
        grid-column: span 4;
      }

      @include tablet {
        grid-column: span 6;
      }
    }

    &:nth-child(6) {
      grid-column: span 3;

      @include desktop {
        grid-column: span 4;
      }

      @include tablet {
        grid-column: span 6;
      }
    }

    @include desktop {
      grid-column: span 4;
    }

    @include tablet {
      grid-column: span 6;
    }
  }

  &__item--pic5 {
    background-image: url("../img/icon-5.svg");
  }

  &__item--pic6 {
    background-image: url("../img/icon-6.svg");
  }

  &__item--pic7 {
    background-image: url("../img/icon-7.svg");
  }

  &__item--pic8 {
    background-image: url("../img/icon-8.svg");
  }

  &__item--pic9 {
    background-image: url("../img/icon-9.svg");
  }

  &__item--pic10 {
    background-image: url("../img/icon-10.svg");
  }

  &__item--pic11 {
    background-image: url("../img/icon-11.svg");
  }

  &__item--pic12 {
    background-image: url("../img/icon-12.svg");
  }

  &__item--pic13 {
    background-image: url("../img/icon-13.svg");
  }

  &__item--pic14 {
    background-image: url("../img/icon-14.svg");
  }

  &__name {
    margin: 0;
    margin-bottom: 12px;
    font-weight: 400;
    font-size: 15px;
    line-height: 130%;
    color: var(--light-color);
  }

  &__num {
    margin: 0;
    font-weight: 700;
    font-size: 28px;
    line-height: 130%;
    color: var(--light-color);

    @include mobile {
      font-size: 20px;
      line-height: 130%;
    }
  }

  @include tablet {
    padding: 65px 0 70px;
  }

  @include mobile {
    padding: 65px 0 55px;
  }
}
