.hero {
  position: relative;
  padding-bottom: 15px;
  background-color: var(--blue-color);

  &__top {
    position: relative;
    z-index: 0;
    padding-top: 110px;
    padding-bottom: 116px;
    background-image: url("../img/hero-bg.png");
    background-position: top center;
    background-size: cover;
    background-repeat: no-repeat;

    @include tablet {
      padding-top: 135px;
      padding-bottom: 140px;
    }

    @include mobile {
      padding-top: 170px;
      padding-bottom: 90px;
    }
  }

  &__top-wrapper {
    max-width: 885px;

    @include tablet {
      max-width: 800px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 600;
    font-size: 28px;
    line-height: 130%;
    color: var(--light-color);

    @include tablet {
      font-size: 24px;
    }

    @include mobile {
      font-size: 20px;
      line-height: 130%;
    }
  }

  &__title-upp {
    display: block;
    margin: 0;
    padding-bottom: 16px;
    font-weight: 800;
    font-size: 62px;
    line-height: 85px;
    text-transform: uppercase;
    color: var(--light-color);

    @include tablet {
      font-size: 52px;
      line-height: 71px;
    }

    @include mobile {
      font-size: 32px;
      line-height: 44px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 500;
    font-size: 22px;
    line-height: 150%;
    color: var(--light-color);

    @include tablet {
      font-size: 17px;
    }

    @include mobile {
      font-size: 15px;
      line-height: 150%;
    }
  }

  &__image {
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
  }

  &__bottom {
    padding: 30px 40px;

    @include desktop {
      padding: 30px 0;
    }
  }

  &__bottom-container {
    display: flex;
    align-items: flex-start;

    @include desktop {
      flex-direction: column;
    }
  }

  &__bottom-descr {
    margin-right: 60px;
    width: calc(100% - 615px);

    @include desktop {
      margin-right: 0;
      margin-bottom: 40px;
      width: 100%;
    }
  }

  &__bottom-list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    width: 52%;
    gap: 30px;

    @include tablet {
      width: 100%;
      gap: 45px 30px;
    }

    @include mobile {
      gap: 25px 30px;
    }
  }

  &__bottom-item {
    grid-column: span 3;
    padding-top: 60px;
    background-size: 52px 52px;
    background-repeat: no-repeat;
  }

  &__bottom-item--pic1 {
    background-image: url("../img/icon-1.svg");
  }

  &__bottom-item--pic2 {
    background-image: url("../img/icon-2.svg");
  }

  &__bottom-item--pic3 {
    background-image: url("../img/icon-3.svg");
  }

  &__bottom-item--pic4 {
    background-image: url("../img/icon-4.svg");
  }

  &__bottom-name {
    margin: 0;
    margin-bottom: 12px;
    font-weight: 400;
    font-size: 15px;
    line-height: 130%;
    color: var(--light-color);

    @include mobile {
      font-size: 13px;
      line-height: 130%;
    }
  }

  &__bottom-num {
    margin: 0;
    font-weight: 700;
    font-size: 36px;
    line-height: 130%;
    color: var(--light-color);

    @include mobile {
      font-size: 20px;
      line-height: 130%;
    }
  }

  @include tablet {
    padding-bottom: 25px;
  }

  @include mobile {
    padding-bottom: 15px;
  }
}
