.steps {
  padding-bottom: 100px;

  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 36px;
    line-height: 150%;
    color: var(--dark-color);

    @include tablet {
      margin-bottom: 20px;
      font-size: 32px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 24px;
      line-height: 150%;
    }
  }

  &__list {
    counter-reset: counter-benefit;
    display: flex;
    gap: 30px;

    @include tablet {
      flex-direction: column;
    }
  }

  &__item {
    position: relative;
    width: 25%;

    &::before {
      counter-increment: counter-benefit;
      content: "Шаг "counter(counter-benefit);
      display: inline-block;
      margin-bottom: 16px;
      padding: 10px 20px;
      font-weight: 700;
      font-size: 28px;
      line-height: 130%;
      color: var(--blue-color);
      background-color: var(--lightblue-color);
    }

    &:not(:last-child) {
      &::after {
        content: "";
        position: absolute;
        right: -30px;
        top: 30px;
        z-index: -1;
        display: block;
        border-top: 1px dashed var(--blue-color);
        width: 100%;
        height: 2px;

        @include tablet {
          display: none;
        }
      }
    }

    @include tablet {
      width: 100%;
    }
  }

  &__text {
    margin: 0;
    font-size: 20px;
    line-height: 150%;

    @include tablet {
      font-size: 17px;
      line-height: 150%;
    }

    @include mobile {
      font-size: 15px;
      line-height: 150%;
    }
  }

  @include tablet {
    padding-bottom: 50px;
  }

  @include mobile {
    padding-bottom: 70px;
  }
}
