.benefit {
  padding: 30px 0;

  &__title {
    margin: 0;
    margin-bottom: 30px;
    width: 50%;
    font-weight: 700;
    font-size: 36px;
    line-height: 150%;
    color: var(--blue-color);

    @include tablet {
      margin-bottom: 25px;
      width: 100%;
      font-size: 32px;
      line-height: 150%;
    }

    @include mobile {
      margin-bottom: 15px;
      font-size: 24px;
      line-height: 150%;
    }
  }

  &__wrapper {
    columns: auto 2;
    column-gap: 30px;
    margin-bottom: 55px;

    @include tablet {
      columns: inherit;
      margin-bottom: 30px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark-color);

    @include tablet {
      font-size: 17px;
      line-height: 150%;
    }

    @include mobile {
      margin-bottom: 20px;
      font-size: 15px;
      line-height: 150%;
    }
  }

  &__list {
    counter-reset: counter-benefit;
    list-style-type: none;
  }

  &__item {
    display: flex;
    margin-bottom: 30px;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark-color);

    &::before {
      counter-increment: counter-benefit;
      content: counter(counter-benefit);
      display: inline-flex;
      flex: 1 0 auto;
      align-items: center;
      justify-content: center;
      margin-right: 30px;
      width: 57px;
      height: 54px;
      font-weight: 700;
      font-size: 28px;
      line-height: 130%;
      color: var(--blue-color);
      background-color: rgb(0 177 201 / 8%);

      @include tablet {
        margin-right: 25px;
      }
    }

    @include tablet {
      margin-bottom: 25px;
      font-size: 17px;
      line-height: 150%;
    }

    @include mobile {
      margin-bottom: 20px;
      font-size: 15px;
    }
  }

  @include tablet {
    padding: 60px 0 30px;
  }

  @include mobile {
    padding: 70px 0 30px;
  }
}
